

* {
  box-sizing: border-box;
}

.ranking-wrap {
  min-height: 100vh;
  padding: 40px 16px;
  background-color: black;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.ranking-title {
  text-align: center;
  font-size: 2.2rem;
  color: var(--pink-dark);
  margin: 0 0 10px;
}

.ranking-subtitle {
  text-align: center;
  color: white;
  margin-bottom: 40px;
}

.ranking-list {
  display: grid;
  gap: 16px;
  max-width: 700px;
  margin: 0 auto;
}

.ranking-card {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 14px 20px;
  border-radius: 14px;
  background: white;
  box-shadow: 0 4px 10px rgba(0,0,0,0.08);
  transition: transform 0.2s ease;
}
.ranking-card:hover {
  transform: scale(1.02);
}

.rank-badge {
  min-width: 40px;
  min-height: 40px;
  border-radius: 50%;
  background: var(--pink);
  color: rgb(19, 18, 18);
  font-weight: bold;
  font-size: 1.2rem;
  display: grid;
  place-items: center;
}
.rank-image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  border: 3px solid var(--pink);
}
.rank-info h3 {
  margin: 0;
  font-size: 1.2rem;
  color: black;
}
.rank-info p {
  margin: 4px 0 0;
  color: black;
}

/* Special colors for top 3 ranks */
.rank-1 .rank-badge { background: var(--gold); color: #000; }
.rank-2 .rank-badge { background: var(--silver); color: #000; }
.rank-3 .rank-badge { background: var(--bronze); color: #fff; }
